.slider-image {
  padding: 0;
  margin: 0;
  position: relative;
  overflow: hidden;
}
.slider-image p {
    margin: 0;
    padding: 0;
}
.slider-image .image-list {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}
.slider-image .image-item {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    height: 100%;
    width: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
}
.slider-image .image-item .title {
      text-align: center;
      font-weight: bold;
      margin-top: 6rem;
      margin-bottom: 2rem;
      line-height: 3rem;
      font-size: 3rem;
      -webkit-transition: all .5s;
      transition: all .5s;
}
.slider-image .image-item .subtitle {
      text-align: center;
      font-weight: bold;
      margin-bottom: 4rem;
      height: 16px;
      -webkit-transition: all .5s;
      transition: all .5s;
      line-height: 1.2rem;
      font-size: 1.2rem;
}
.slider-image .image-item .button {
      width: 168px;
      height: 43px;
      text-align: center;
      line-height: 43px;
      margin: auto;
      display: block;
      font-weight: bold;
      position: relative;
      -webkit-transition: all .5s;
      transition: all .5s;
}
.slider-image .image-item .button .button-line {
        position: absolute;
        display: block;
        opacity: 0;
        -webkit-transition: all .5s;
        transition: all .5s;
        -webkit-transition-timing-function: ease;
                transition-timing-function: ease;
}
.slider-image .image-item .button .button-line--top {
        height: 1px;
        width: 170px;
        top: -1px;
        left: -170px;
}
.slider-image .image-item .button .button-line--bottom {
        height: 1px;
        width: 170px;
        bottom: -1px;
        right: -170px;
}
.slider-image .image-item .button .button-line--left {
        width: 1px;
        height: 45px;
        bottom: -45px;
        left: -1px;
}
.slider-image .image-item .button .button-line--right {
        width: 1px;
        height: 45px;
        top: -45px;
        right: -1px;
}
.slider-image .image-item .button:hover .button-line {
        opacity: 1;
}
.slider-image .image-item .button:hover .button-line--top {
        left: -1px;
}
.slider-image .image-item .button:hover .button-line--bottom {
        right: -1px;
}
.slider-image .image-item .button:hover .button-line--left {
        bottom: -1px;
}
.slider-image .image-item .button:hover .button-line--right {
        top: -1px;
}
.slider-image .image-item .button-full {
      position: absolute;
      top: 0;
      left: 0;
      margin: 0;
      width: 100%;
      height: 100%;
}
.slider-image .image-item .button-full i {
        display: none !important;
}
.slider-image .play-group {
    height: 20px;
    line-height: 20px;
    background: rgba(51, 51, 51, 0.51);
    position: absolute;
    text-align: center;
    bottom: 20px;
    padding: 0 10px;
    border-radius: 15px;
    left: 50%;
}
.slider-image .play-group .play-button {
      display: inline-block;
      width: 10px;
      height: 10px;
      background: #ffffff;
      border-radius: 50%;
      background: #696969;
      cursor: pointer;
}
.slider-image .play-group .play-button.active {
        background: #ffffff;
}
@media screen and (max-width: 600px) {
.slider-image .image-item .title {
    font-size: 2.5rem;
}
}
